import React from 'react';
import ReactDOM from 'react-dom';
import './static/css/index.css';
import './static/css/bootstrap.min.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter } from 'react-router-dom';
import { Provider } from 'react-redux';
import { combineReducers, createStore } from 'redux';

let iniValue = [{ id : 10, name : 'free socks', quan : 1}]
let iniAlert = true;

function reducer(state = iniValue, doAction) {
  if (doAction.type === 'productAdd') {
    
    let found = state.findIndex((a)=>{ return a.id === doAction.payload.id });

    if (found >= 0) {
      let copy = [...state]
      copy[found].quan++
      return copy
    } else {
      let copy = [...state]
      copy.push(doAction.payload);
      return copy
    }
  } else if (doAction.type === 'quanAdd') {
    let copy = [...state];
    copy[doAction.payload].quan++;
    return copy

  } else if (doAction.type === 'quanSub') {
    let copy = [...state];
    if (copy[doAction.payload].quan > 1) {
      copy[doAction.payload].quan--;
    } else {
      alert("minimum quantity")
    }
    return copy

  } else {
    return state
  }
}

function reducer2(state = iniAlert, doAction) {
  if (doAction.type === 'alertClose') {
    state = false;
    return state
  } else {
    return state
  }
}

let store = createStore(combineReducers({reducer, reducer2}));

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

reportWebVitals();
