import { Table } from 'react-bootstrap';
import { connect } from 'react-redux';

const Cart = (props) => {
  return (
    <>
    <Table responsive="sm">
      <thead>
        <tr>
          <th>Product No.</th>
          <th>Product Name</th>
          <th>Quantity</th>
          <th>Quantity Change</th>
        </tr>
      </thead>
      <tbody>
        {
          props.state.map((a, i)=>{
            return (
              <tr key={i}>
                <td>{ a.id }</td>
                <td>{ a.name }</td>
                <td>{ a.quan }</td>
                <td>
                  <button onClick={()=>{
                    props.dispatch({ type : 'quanAdd', payload : a.id})
                    }}> +
                  </button>
                  <button onClick={()=>{
                    props.dispatch({ type : 'quanSub', payload : a.id})
                    }}> -
                  </button>
                </td>
              </tr>
            )
          })
        }
      </tbody>
    </Table>

    { props.alertOpen === true
      ? (
        <div className="inventory-alert">
          <h4>20% OFF NOW</h4>
          <button onClick={()=>{
            props.dispatch({type : 'alertClose'})
          }}>close</button>
        </div>
        )
      : null
    }


    </>
  )
}

function stateToProps(state) {
  return {
    state : state.reducer,
    alertOpen : state.reducer2
  }
}

export default connect(stateToProps)(Cart)
